@media (max-width: (@tablet-width - 1px)) {
  .reviews .container {
    padding: 0 30px;
  }
}

.reviews .container {
  padding-bottom: 51px;
  margin-bottom: 35px;
  position: relative;

  &::before {
    content: "";
    position: absolute;
    width: 100%;
    height: 7px;
    background-image: url("../img/bg-zigzag-line.svg");
    background-size: 100% 7px;
    background-position: 8px 0;
    background-repeat: repeat-x;
    bottom: 0;
    left: 0;
  }
}

.reviews__title {
  font-size: 25px;
  font-weight: 700;
  line-height: 30px;
  margin: 0;
  margin-bottom: 24px;
  margin-left: -3px;
}

.reviews__item {
  margin: 0;
  margin-bottom: 29px;
}

.reviews__text {
  line-height: 30px;
  color: @gray;

  margin: 0;
  margin-bottom: 27px;
}

.reviews__author {
  font-style: normal;
  font-size: 17px;
  font-weight: 700;
}

.reviews__author span {
  font-weight: 400;
  line-height: 30px;
  color: @gray;

  display: block;
  padding-top: 3px;
}

.reviews__toggles {
  display: flex;
  margin-bottom: 38px;
}

.reviews__toggle-prev,
.reviews__toggle-next {
  padding: 0;
  box-sizing: border-box;
  width: 130px;
  height: 52px;
  background-color: @white;
  border: 1px solid @light-gray;
  cursor: pointer;
  background-repeat: no-repeat;
  background-position: 37px 12px;

  &:hover {
    opacity: 0.6;
  }

  &:active {
    opacity: 0.3;
  }
}

.reviews__toggle-prev {
  background-image: url("../img/icon-left-arrow.svg");
}

.reviews__toggle-next {
  background-image: url("../img/icon-right-arrow.svg");
}

@media (min-width: @tablet-width) {
  .reviews .container {
    margin-bottom: 60px;
    padding-top: 77px;
    padding-bottom: 103px;

    background-image: url("../img/review@1x.jpg");
    background-repeat: no-repeat;
    background-position: 0 0;

    &::before {
      height: 17px;
      background-size: 100% 17px;
      background-position: 20px 0;
    }

    @media (min-resolution: @retina-dpi), (min-resolution: @retina-dppx) {
      background-image: url("../img/review@2x.jpg");
      background-size: 350px auto;
    }
  }

  .reviews__title {
    font-size: 43px;
    line-height: 50px;

    margin-bottom: 49px;
    margin-left: -5px;
  }

  .reviews__text {
    font-size: 17px;
    margin-bottom: 57px;
  }

  .reviews__author {
    font-size: 18px;

    display: block;
    width: 260px;
  }

  .reviews__author span {
    font-size: 17px;
    display: block;
    padding-top: 4px;
  }

  .reviews__toggles {
    margin: 0;

    position: absolute;
    right: 50px;
    bottom: 148px;
  }

  .reviews__toggle-prev,
  .reviews__toggle-next {
    width: 50px;
    height: 26px;

    border: none;
    background-position: 0 0;
  }

  .reviews__toggle-next {
    margin-left: 37px;
    margin-right: 19px;
  }

  .reviews__button-write {
    width: 175px;

    position: absolute;
    right: 50px;
    top: 79px;
  }
}

@media (min-width: @desktop-width) {
  .reviews {
    padding-bottom: 0;
    margin-bottom: 70px;
  }

  .reviews .container {
    background-position: 50px 7px;
    padding-bottom: 157px;
    padding-left: 225px;
    position: relative;

    &::before {
      content: "";
      position: absolute;
      width: 1050px;
      height: 23px;
      background-image: url("../img/bg-zigzag-line.svg");
      background-size: auto 21px;
      background-position: -28px 0;
      background-repeat: repeat-x;
      bottom: 0;
      left: 51px;
    }
  }

  .reviews__title {
    font-size: 53px;
    line-height: 57px;
  }

  .reviews__text {
    width: 770px;
    margin-bottom: 39px;
  }

  .reviews__author {
    font-size: 17px;
  }

  .reviews__author span {
    padding-top: 3px;
  }

  .reviews__button-write {
    top: 86px;
  }

  .reviews__toggles {
    bottom: 202px;
  }
}
